
import Box from 'vux/src/components/box/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


export default {
components: {
XButton,
Box,
Divider,
Group,
XInput,
Selector,
Toast
},
data () {
return {
addUrl: this.$baseUrl + '/students',
loading: false,
subjects: [
'',
'',
'',
'',
'',
''
],
form: {
number: null,
subject: '',
name: null,
phone: null,
user: {
username: this.$route.query.username
}
}
}
},
methods: {
addStudent () {
this.$axios.post(this.addUrl, this.form).then((res) => {
this.$vux.toast.text('')
sessionStorage.setItem('token', null)
this.$router.push('/lab')
}).catch((error) => {
console.debug(error.response)
if (error.response.status === 400) {
this.$vux.toast.text('')
}
})
}
}
}



